	module.exports = {"data":{"markdownRemark":{"html":"<p>I lay in bed until 10</p>\n<p>coughing up a lung</p>\n<p>it is not a beautiful song</p>\n<p>the day crawls by</p>\n<p>does time expand in a body under malaise?</p>\n<p>yes, I think so</p>\n<p>simultaneously listless and restless</p>\n<p>tired and wired</p>\n<p>my mind craves engagement</p>\n<p>the body denies the request</p>\n<p>I fall asleep on the couch</p>\n<p>many times</p>\n<p>craving fresh air and movement</p>\n<p>I take a walk to nowhere at 8:30 PM</p>\n<p>I pass pizza places, bookstore and restaurants</p>\n<p>clamoring with the Thursday night crowds</p>\n<p>tiny tribes gathered on street corners</p>\n<p>sharing libation-inspired conversation</p>\n<p>passing cigarettes back and forth</p>\n<p>their tips glowing a pronounced, sharp orange</p>\n<p>I walk to the park</p>\n<p>and see dogs in jackets</p>\n<p>running, barking, playing on the cold January ground</p>\n<p>I share furtive glances with passersby</p>\n<p>bundled in jackets</p>\n<p>seeking a destination warmer than</p>\n<p>their current one</p>\n<p>It is cold and quiet</p>\n<p>my nostrils fill with crisp air</p>\n<p>my walk comes to an end</p>\n<p>I am better for it</p>","timeToRead":1,"tableOfContents":"","fields":{"tagSlugs":["/tags/poetry/"]},"frontmatter":{"title":"Sick Day","tags":["poetry"],"date":"January 17, 2019","author":{"id":"Evan Nichols","bio":"Software Engineer out of NYC writing about programming, web development and more.","avatar":{"children":[{"__typename":"ImageSharp","responsiveResolution":{"src":"/static/evan-9e6a6b67e58d77cd1bc023a71bd56378-c4f5a.jpg","srcSet":"/static/evan-9e6a6b67e58d77cd1bc023a71bd56378-c4f5a.jpg 1x,\n/static/evan-9e6a6b67e58d77cd1bc023a71bd56378-27112.jpg 1.5x,\n/static/evan-9e6a6b67e58d77cd1bc023a71bd56378-f5e46.jpg 2x,\n/static/evan-9e6a6b67e58d77cd1bc023a71bd56378-592c8.jpg 3x"}}]}}}}},"pathContext":{"slug":"/posts/sick-day/"}}

